export default {
	data() {
		return {
			avatarUrl:'',
			nickName:'',
			icon:'',
			name:'',
			phone:'',
			gender:'',
			ID:''
			
		}
	},
	onLoad() {
		var that = this
		uni.getStorage({
			key:'token',
			success(res) {
				console.log(res)
				that.name = res.data.name
				that.phone = res.data.phone
				if(res.data.sex == true){
					that.gender = '女'
				}else{
					that.gender = '男'
				}
				that.ID = res.data.companyId
			}
		})
	},
	methods: {
		//获取输入框输入框值
		nameInput:function(event){
			this.name = event.target.value
		},
		genderInput:function(event){
			this.gender = event.target.value
			if(this.gender == "男"){
				this.sex = false
			}
			else{
				this.sex = true
			}
		},
		phoneInput:function(event){
			this.phone = event.target.value
		},
		IDInput:function(event){
			this.ID = event.target.value
		},
		//保存个人信息
		toEdit(){
			var that = this
			uni.getStorage({
				key:'token',	
			   success: function (res) {
				   console.log(res)
				   that.icon = res.data.icon,
				   that.nickName = res.data.nickName
				   that.token = res.data.token
				  that.toUp(that.nickName,that.icon)
				},
				fail() {
					that.toUp()
				}
			})
		
		},
		//上传个人信息
		toUp(nickName,icon){
			var that = this
			console.log(nickName)
			console.log(icon)
			if(this.name == undefined || this.gender == undefined || this.phone == undefined || this.ID == undefined){
				uni.showToast({
				    title: '输入内容不能为空',
				    icon:"none"
				});
			}else{
			   uni.request({
				url:'http://112.74.86.165:8082/wechatUserinfo',
				method:'PUT',
				data:{
					companyId: this.ID,
					icon: this.icon,
					name: this.name,
					nickname: this.nickName,
					phone: this.phone,
					sex: this.sex
				},
				header:{
					Authorization:that.token
				},
				success(res) {
					console.log(res)
					wx.setStorage({
						key:'companyId',
						data:{	
							companyId: that.ID,
							nickname: res.data.data.nickname,
							name: res.data.data.name,
							phone: res.data.data.phone,
							sex: res.data.data.sex,
							icon:res.data.data.icon
						},
					})
					uni.showToast({
						title:'保存成功',
					})
					uni.switchTab({
						url:'./user'
					});
				}
			   })
			   }
		}
	}
}
